} */
.barchart-yAxis, .barchart-xAxis {
  stroke: #9B9EAC;
  stroke-width: .1; }

.barchart-yAxis > line {
  stroke: #DEDEDE;
  stroke-width: 1; }

.bar-chart__text-legend {
  color: #74798C; }

/**
* _page-spinner.scss 1.0 decembre 2021 by Alexandre CHARLIER  
*
* Author : Alexandre CHARLIER.
* GitHub : https://github.com/alexandre34000/AlexandreCharlier_3_02042021.
* GitHub Pages : https://alexandre34000.github.io/AlexandreCharlier_3_02042021/.
* Theme Name : sportSee .
* Description : Stylesheet pour le spinner d'attente.
* Date: decembre 2021. 
*/
.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: #FFFFFF; }

.cube-grid {
  width: 100px;
  height: 100px;
  background-color: white; }

.cube-grid .cube {
  width: 33%;
  height: 33%;
  background-color: #FF0101;
  float: left;
  -webkit-animation: cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: cubeGridScaleDelay 1.3s infinite ease-in-out; }

.cube-grid .cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.cube-grid .cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.cube-grid .cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

.cube-grid .cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.cube-grid .cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.cube-grid .cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.cube-grid .cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s; }

.cube-grid .cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.cube-grid .cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

@-webkit-keyframes cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

@keyframes cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

